@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

html, body {
  font-family: 'Roboto', sans-serif;
  margin: 0 0;
  padding: 0;
  overflow: hidden;

}
.App {
  background: black;
  color: white;
  width: 100vw;
  height: 100vh;
  overflow-y: auto;
}

.backHomeBtn {
  text-decoration: none;
  color: white;
  cursor: pointer;
  user-select: none;
  background: #333;
  justify-content: center;
  overflow: hidden;
  align-items: center;
  display: flex;
  width: 100%;
  min-width: 100px;
  height: inherit;
}